import { Injectable ,OnInit} from '@angular/core';
import { ShareInfo } from './main.interface';

declare var wx:any;

@Injectable({
  providedIn: 'root'
})
export class WechatService {

  constructor() { 

  }

  OnInit(){
    
  }

  readyShare(){
    let that = this;

    var defaultShareInfo:ShareInfo = {
      title: "UVCSI超人投票",
      desc: "每个人都是超级英雄，在这里！",
      link: location.href,
      imgUrl: "http://www.jonnypeng.com/imgs/logo.png",
      success:function (){
        console.log("share is ok");
      }
    }

    var value = {
      url: location.href
    }

    $.ajax({
      url: "http://www.ulifecity.com:4001/wechat_jsdk",
      type: 'post',
      contentType:"application/json",
      data:JSON.stringify(value),
      success: function (jsons) {
        if (jsons.code !== 1) {
          console.log(jsons.msg);
        }
        var data = jsons.result;
        console.log(data);
        wx.config({
          debug: false,
          appId: data.appId,
          timestamp: data.timestamp,
          nonceStr: data.nonceStr,
          signature: data.signature,
          jsApiList: [
            'onMenuShareTimeline',
            'onMenuShareAppMessage'
          ]
        });
      }
    });
  }

  getShare(v:ShareInfo = {
      title:"超级英雄投票DEMO",
      desc:"每个人都有无限的潜力",
      imgUrl:"https://s2.ax1x.com/2019/04/06/AW21YQ.png",
      link:"http://www.jonnypeng.com/vote/dist/info",
      success:function (){
        console.log("ok");
      }
  }){
    wx.ready(function () {   //需在用户可能点击分享按钮前就先调用
      wx.onMenuShareAppMessage({
        title: v.title,
        desc: v.desc, 
        link: v.link,
        imgUrl: v.imgUrl,
        success: v.success
      })
      wx.onMenuShareTimeline({
        title: v.title,
        desc: v.desc,
        link: v.link,
        imgUrl: v.imgUrl,
        success: v.success
      })
    });
  }

}
