import { Component, OnInit ,AfterViewInit} from '@angular/core';
import { MainService } from '../main.service';
import { WechatService } from '../wechat.service';

class Player{
  player_id:number;
  name:string;
  headimg:string;
  star:number;
}

@Component({
  selector: 'app-ranking',
  templateUrl: './ranking.component.html',
  styleUrls: ['./ranking.component.css']
})
export class RankingComponent implements OnInit {
  coverImg:string = "http://placehold.it/640x320";
  offset:number = 0; 
  players:Player[]=[];
  isBottom:boolean = false;
  isRequestServer:boolean = true;
  searchClose:boolean = true;
  playerCount:number;

  constructor(private service:MainService,private wechatService:WechatService) { }

  ngOnInit(){
    this.onResetPlayers({searchClose:true});
  }

  onResetPlayers(event?:any){
    this.searchClose = event.searchClose;
    this.offset = 0;
    let value = {
      offset:this.offset
    }
    this.service.getplayer(value).subscribe((v)=>{
      console.log(v);
      this.players = v.result.players;
      this.playerCount = v.result.playerCount;
    },(err)=>{
      console.log(err);
    });
  }

  ngAfterViewInit(){
    this.wechatService.getShare({
     title:"UVCSI Superhero 当前排名榜单" ,
     desc:"我知道你想报名，但是我们需要看你的邀请函！！！",
     imgUrl:"https://s2.ax1x.com/2019/04/06/AW21YQ.png",
     link:"http://www.jonnypeng.com/vote/dist/ranking",
     success:function (){
       console.log("ok");
     }
    });
    // let anchor:string = this.service.showRankingAnchor();
    // setTimeout(()=>{
    //   window.location.href =`http://www.jonnypeng.com/vote/dist/ranking#${anchor}`;
    // },1000);
  }

  onReceive(event:any){
    this.players = event.players;
    this.searchClose = event.searchClose;
  }

  onScroll(event){
    let that = this;
    //console.log(event.target.scrollTop);   //偏移高度 2577
    /*
     event.target.scrollTop:2577
     event.target.scrollHeight:3132 
     event.target.clientHeight:555
    */
   let clientHeight = event.target.clientHeight;
   let scrollHeight = event.target.scrollHeight;
   let offseetHeight = scrollHeight - clientHeight;
   let scrollTop = event.target.scrollTop;
   

   
   if(scrollTop <= offseetHeight - 70){
     console.log("已经远离底部");
      that.isBottom = false; 
   }

   if(scrollTop > offseetHeight - 70){
     if(!that.isBottom && that.searchClose && that.offset < that.playerCount){
        that.isBottom = true;
        console.log(that.isBottom,"到达底部");
        gotoServer();
     }
   }

   function gotoServer(){
    that.offset+=10;
    let value = {
      offset:that.offset
    }
    that.service.getplayer(value).subscribe((v)=>{
      console.log(v);
      that.players = that.players.concat(v.result.players);
    },(err)=>{
      console.log(err);
    }); 
   }


  }

}
