import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { InfoComponent } from './info/info.component';
import { RankingComponent } from './ranking/ranking.component';
import { JoinComponent } from './join/join.component';
import { PersonalComponent } from './personal/personal.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { WechatUserinfoComponent } from './wechat-userinfo/wechat-userinfo.component';

const routes: Routes = [
  {path:"info",component:InfoComponent},
  {path:"ranking",component:RankingComponent},
  {path:"personal/:player_id",component:PersonalComponent},
  {path:"myinfo/:player_id",component:PersonalComponent},
  {path:"notfound/:player_id",component:NotFoundComponent},
  {path:"join",component:JoinComponent},
  {path:"",redirectTo:"/info",pathMatch:'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
