import { Component, OnInit ,AfterViewInit, EventEmitter,Output} from '@angular/core';
import { AlertInfo,Player } from '../main.interface';
import { MainService } from '../main.service';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
  searchClose:boolean = true;
  alertInfo:AlertInfo;
  players:Player[];
  @Output()outPlayers:EventEmitter<any>;
  @Output()resetPlayers:EventEmitter<any>;
  constructor(private service:MainService) { 
    this.outPlayers = new EventEmitter();
    this.resetPlayers = new EventEmitter();
    this.alertInfo = {
      class:"alert",
      display:false,
      text:""
    }
  }

  ngOnInit() {
  }

  ngAfterViewInit(){
      let that = this;

      $("#search").on("click",onSearch);

      function onSearch(){
      let nameValue:string = <string>$("#name").val();
        if(nameValue===""){
          console.log(1)
            that.alertInfo = {
              class:"alert alert-danger",
              display:true,
              text:"你有未输入的内容"
            }
          }else{
            that.service.getPlayersOfName({name:nameValue}).subscribe(
              (v)=>{
              if(v.code===0){
                that.alertInfo = {
                  class:"alert alert-danger",
                  display:true,
                  text:v.msg
                } 
              }
              that.players = v.result;
            },(err)=>{
              console.log(err);
            },()=>{
              that.searchClose = false;
              that.outPlayers.emit({players:that.players,searchClose:that.searchClose});
            })
        }
      }
  }

  onCloseSearch(){
      $("#name").val("");
      this.searchClose = true;
      this.resetPlayers.emit({searchClose:this.searchClose});
  }
}
