import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, fromEvent } from 'rxjs';
import * as EventEmitter from 'events';
import { Userinfo, UserinfoInterface, ResBody } from './main.interface';

const HOST: string = "http://www.ulifecity.com:4003";

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
    'Authorization': 'my-auth-token'
  })
};


@Injectable({
  providedIn: 'root'
})

export class MainService {
  userinfo: Userinfo;
  rankingAnchor:string;
  boradcastUserinfo:Observable<Userinfo>;
  boradcastEmitter:EventEmitter;

  constructor(private http: HttpClient){
    this.userinfo = new Userinfo();
    this.rankingAnchor = "p0";
    this.boradcastEmitter = new EventEmitter();
    this.boradcastUserinfo = fromEvent(this.boradcastEmitter,"msg");
  }

  getSnsapi(v): Observable<ResBody> {
    return this.http.post<ResBody>(`http://www.ulifecity.com:4001/snsapi`, v, httpOptions);
  }

  serverLogin(): Observable<ResBody> {
    let v: UserinfoInterface = {
      openid: this.userinfo.openid,
      headimgurl: this.userinfo.headimgurl,
      nickname: this.userinfo.nickname
    }
    if (v.openid === undefined) {
      throw "没有openid";
    }
    return this.http.post<ResBody>(`${HOST}/login`, v, httpOptions);
  }

  saveRankingAnchor(v:string){
    this.rankingAnchor = v;
  }

  showRankingAnchor(){
    return this.rankingAnchor;
  }

  saveWechatInfo(v: UserinfoInterface) {
    this.userinfo.headimgurl = v.headimgurl;
    this.userinfo.openid = v.openid;
    this.userinfo.nickname = v.nickname;
    this.syncUserinfo();
  }


  saveID(v) {
    this.userinfo.fans_id = v.fans_id;
    this.userinfo.player_id = v.player_id;
    this.userinfo.time = v.time;
  }

  saveTime(time:string){
    this.userinfo.time = time; 
  }

  showTime(){
    return this.userinfo.time; 
  }

  savePlayer(v){
    this.userinfo.name = v.name;
    this.userinfo.headimg = v.headimg;
    this.userinfo.phone = v.phone;
    this.userinfo.message = v.message;
    console.log(this.userinfo);
  }

  savePlayerId(v){
    this.userinfo.player_id = v.player_id;
  }

  getjoin(): Observable<ResBody> {
    let v = {
      openid:this.userinfo.openid,
      name:this.userinfo.name,
      phone:this.userinfo.phone,
      headimg:this.userinfo.headimg,
      message:this.userinfo.message
    }
    return this.http.post<ResBody>(`${HOST}/join`,v, httpOptions);
  } 

  getVote(v):Observable<ResBody>{
    return this.http.post<ResBody>(`${HOST}/vote`,v,httpOptions);
  }

  getUserinfo() {
    return this.userinfo;
  }

  getplayer(v): Observable<ResBody> {
    return this.http.post<ResBody>(`${HOST}/getplayer`, v, httpOptions);
  }

  getSinglePlayer(v): Observable<ResBody> {
    return this.http.post<ResBody>(`${HOST}/get_single_personal`, v, httpOptions);
  }

  getPlayersOfName(v):Observable<ResBody>{
    return this.http.post<ResBody>(`${HOST}/name`,v,httpOptions)
  }

  syncUserinfo(){
    this.boradcastEmitter.emit("msg",this.userinfo);
  }


  getObservableUserinfo():Observable<UserinfoInterface>{
    return this.boradcastUserinfo;
    //let that = this;
    // return Observable.create(obs=>{
    //     obs.next(that.userinfo)
    //     if(that.userinfo.openid === undefined){
    //       setTimeout(()=>{
    //         obs.next(that.userinfo)
    //       },1000)
    //     }
    // })
  }
}
