import { Component, OnInit, AfterViewInit, Input } from '@angular/core';
import { MainService } from '../main.service';
import { Userinfo, UserinfoInterface } from '../main.interface';
import { WechatService } from '../wechat.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-tabbar',
  templateUrl: './tabbar.component.html',
  styleUrls: ['./tabbar.component.css']
})
export class TabbarComponent implements OnInit {
  player_id:number;
  link: string;
  constructor(
    private service: MainService,
    private wechatService:WechatService,
    private location:Location,
    private router:Router) {
      let subscribe = this.service.getObservableUserinfo().subscribe((userinfo)=>{
        this.player_id = userinfo.player_id;
        this.link = `/myinfo/${this.player_id}`;
        this.wechatService.readyShare();
        //subscribe.unsubscribe();
        console.log("tabbar",userinfo);
      })
  }

  ngOnInit() {
  }

  ngAfterViewInit() {
  }


}
