import { Component, OnInit ,AfterViewInit,OnDestroy} from '@angular/core';
import { MainService } from '../main.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-delete-me',
  templateUrl: './delete-me.component.html',
  styleUrls: ['./delete-me.component.css']
})
export class DeleteMeComponent implements OnInit {
  openid: string;
  subscribe:Subscription;
  constructor(private service: MainService) { 
    this.subscribe = this.service.getObservableUserinfo().subscribe((userinfo)=>{
      console.log(userinfo);
      this.openid = userinfo.openid;
    }) 
  }

  ngOnInit() {
    this.service.syncUserinfo();
  }

  ngAfterViewInit(){
  }

  ngOnDestroy(): void {
    this.subscribe.unsubscribe();
  }



  onDeleteMe() {
    let value = JSON.stringify({ openid: this.openid });
    $.ajax({
      url: "http://www.ulifecity.com:4003/delete-me",
      type:"post",
      contentType: "application/json",
      data: value,
      success: function (v) {
        console.log(v);
        window.location.href = window.location.href; 
      },
      error: function (err) {
        console.log(err);
      }
    })
  }

}
