import { Component, OnInit,AfterViewInit} from '@angular/core';
import { MainService } from '../main.service';
import { Router ,ActivatedRoute } from '@angular/router';
import { Player, Userinfo, ShareInfo, ResBody, AlertInfo } from '../main.interface';
import { WechatService } from '../wechat.service';

@Component({
  selector: 'app-personal',
  templateUrl: './personal.component.html',
  styleUrls: ['./personal.component.css']
})
export class PersonalComponent implements OnInit {
  player:Player;
  time:string;
  herPlayerId:number | string;
  userinfo:Userinfo;
  alertInfo:AlertInfo;

  constructor(private service:MainService,private route:ActivatedRoute,private router:Router,private wechatService:WechatService) { 
    this.player = {
      headimg:"",
      name:"未知用户",
      message:"未知信息",
      player_id:0,
      star:0
    };
    this.alertInfo = {
      display:false,
      class:"alert-success",
      text:"投票成功"
    }
  }

  ngOnInit() {
    this.getTime();
    this.herPlayerId = this.route.snapshot.paramMap.get("player_id");
    this.service.saveRankingAnchor(`p${this.herPlayerId}`);  //保存锚点
    if(this.herPlayerId!=="0"){
      let playerInfo = this.service.getSinglePlayer({player_id:this.herPlayerId}).subscribe(
        (v)=>{
          console.log(v)
          console.log(this.player);
          this.afterGetSingPlayer(v);
        },
        (err)=>{
          console.log(err);
        }
      );
    }else{
      this.router.navigate(["/notfound","0"])
    }
  }

  afterGetSingPlayer(v:ResBody){   //数据获取成功后的钩子
          this.player = v.result[0];
          this.getWechatShare();
  }


  getTime(){
    let now = new Date();
    let year:number = now.getFullYear();
    let month:number | string = now.getMonth() + 1; 
    month = month < 10 ? `0${month}` : `${month}`;
    let date:number | string = now.getDate();
    date = date < 10 ? `0${date}` : `${date}`;
    this.time = `${year}-${month}-${date}`;
    console.log("今天的日期是" + this.time);
  }

  getUserinfo(){
    this.userinfo = this.service.getUserinfo();
    console.log(this.userinfo);
  }

  ngAfterViewInit(): void {
    this.getUserinfo();
  }

  onVote(){
    let that = this;
    let time = this.userinfo.time;  //数据记录的事件
    if(this.time!==time){
      gotoServer();
    }else{
      //alert("你已经投过票了");
      this.alertInfo = {
        display:true,
        class:"alert alert-danger",
        text:"你已经投过票了"
      }
    }
    function gotoServer(){
    that.service.getVote({
      player_id:that.herPlayerId,
      fans_id:that.userinfo.fans_id,
      time:that.time
    }).subscribe((v)=>{
      console.log(v);
      that.player.star+=1;
      that.service.saveTime(that.time);
      that.userinfo.time = that.service.showTime();
      that.alertInfo = {
        display:true,
        class:"alert alert-success",
        text:"投票成功"
      }
    },(err)=>{
      console.log(err);
    })
    }
    
  }

  getWechatShare(){
    let value:ShareInfo = {
      title:`我是${this.userinfo.nickname},请为我的朋友\"${this.player.name}\"投票助力`,
      desc:"come on!come on!",
      link:`http://www.jonnypeng.com/vote/dist/personal/${this.player.player_id}`,
      imgUrl:this.userinfo.headimgurl,
      success:function (){
        console.log("thank you");
      }
    }
    this.wechatService.getShare(value);
  }

}
